<template>
  <div class="form-group">
    <label v-if="label">{{label}}</label>
    <input
      type="checkbox"
      class="form-check-input"
      v-bind="$attrs"
      :name="name"
      :checked="value"
      v-on="$listeners"
      @change="$emit('changeValue', $event.target.checked, name)"
    />
  </div>
</template>
<script>
export default {
  inheritAttrs: false,
  props: {
    value: Boolean,
    label: String,
    name: String
  }
}
</script>
<style scoped>
  input {
    cursor: pointer;
  }
</style>



